import React from 'react';
import classes from './RegisterSellerForm.module.scss';
import { Controller, useForm } from 'react-hook-form';

import { Typography, TextField, Button, Checkbox, FormControlLabel } from '@mui/material';

export const RegisterSellerForm = () => {
  const {
    watch,
    control,
    register,
    handleSubmit,
    formState: { errors },
  } = useForm({
    defaultValues: {
      email: '',
      password: '',
      confirmPassword: '',
      checkbox: false,
    },
    mode: 'onSubmit',
  });

  //eslint-disable-next-line @typescript-eslint/ban-ts-comment
  //@ts-ignore
  const onSubmit = (obj) => {
    const { email, password, checkbox } = obj;
    console.log({ email, password, checkbox });
  };
  return (
    <div className={classes.root}>
      <Typography variant="h5" className={classes.typography}>
        
      </Typography>
      {Boolean(errors.checkbox?.message) && (
        <span style={{ color: 'red' }} role="alert">
          required
        </span>
      )}
      <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
        <TextField
          className={classes.field}
          placeholder={'E-mail'}
          error={Boolean(errors.email?.message)}
          helperText={errors.email?.message}
          size="small"
          // eslint-disable-next-line react/jsx-props-no-spreading
          {...register('email', {
            required: ' e-mail',
            pattern: {
              value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
              message: ' e-mail',
            },
          })}
        />
        <TextField
          classes={{ root: classes.field }}
          type="password"
          autoComplete="off"
          placeholder={''}
          size="small"
          error={Boolean(errors.password?.message)}
          helperText={errors.password?.message}
          // eslint-disable-next-line react/jsx-props-no-spreading
          {...register('password', {
            required: ' ',
            minLength: {
              value: 6,
              message: '   6 ',
            },
            maxLength: {
              value: 14,
              message: '   14 ',
            },
            pattern: {
              value: /^[\w~'`!@#?$%^&*()=+<>|/\\.,:;[\]{} \x22-]{6,25}$/i,
              message: '  ',
            },
          })}
        />
        <TextField
          className={classes.field}
          type="password"
          autoComplete="off"
          placeholder={' '}
          size="small"
          error={Boolean(errors.confirmPassword?.message)}
          helperText={errors.confirmPassword?.message}
          // eslint-disable-next-line react/jsx-props-no-spreading
          {...register('confirmPassword', {
            required: '  ',
            validate: (val) => {
              if (watch('password') != val) {
                return '   ';
              }
            },
            minLength: {
              value: 6,
              message: '   6 ',
            },
            maxLength: {
              value: 14,
              message: '   14 ',
            },
            pattern: {
              value: /^[\w~'`!@#?$%^&*()=+<>|/\\.,:;[\]{} \x22-]{6,25}$/i,
              message: '  ',
            },
          })}
        />
        <Button
          // disabled={!isValid}
          type="submit"
          size="large"
          variant="contained"
          className={classes.buttonSubmit}
        >
          
        </Button>
        <p style={{ fontSize: 10, marginBottom: 10 }}>
                   
        </p>
        <Controller
          name="checkbox"
          control={control}
          rules={{ required: true }}
          render={({ field }) => (
            <FormControlLabel
              className={classes.checkbox}
              // eslint-disable-next-line react/jsx-props-no-spreading
              control={<Checkbox {...field} checked={field.value} />}
              label="          
                 c    , 
                    ."
            />
          )}
        />
      </form>
    </div>
  );
};
